
import { defineComponent, ref } from "vue";
import { useStore } from "@/store";
import ICentroCusto from "@/interfaces/ICentroCusto";
import useNotificator  from "@/hooks/Notificator";
import { POST_CC, PUT_CC } from "@/store/modules/centrocusto/constants/action-type";
import { NotificationType } from "@/interfaces/INotification";
import { Form, Field } from "vee-validate";
import * as Yup from "yup";

export default defineComponent({
  name: "FormularioCCViewComponent",
  components: {
    Form,
    Field,
  },
  props: {
    id: {
      type: String,
    },
  },
  data() {
    const schema = Yup.object().shape({
      iCodigo: Yup.string()
        .required('O campo Cdigo  obrigatrio')
        // .typeError('O campo Cdigo deve conter somente nmeros')
        .matches(/^[0-9]{6}$/i, 'O campo Cdigo deve conter somente nmeros')
        .max(6, 'O campo Cdigo deve conter somente 6 nmeros')
        .min(6, 'O campo Cdigo deve conter 6 nmeros'),
      iNome: Yup.string()
        .required('O campo Nome  obrigatrio'),
    });
    return {
      schema,
    }
  },
  setup(props) {
    const store = useStore();
    const { notify } = useNotificator()
    const centro_custo = ref({} as ICentroCusto);
    if (props.id) {
      const cc = store.state.centrocusto?.ccs.find(
        (ccusto) => ccusto.id == props.id
      );
      centro_custo.value = cc as ICentroCusto;
    }

    return {
      store,
      centro_custo,
      notify,
    };
  },
  methods: {
    saveCentroCusto() {
      if(this.id){
        this.store
          .dispatch(PUT_CC, this.centro_custo)
          .then(() => {
            this.notify(NotificationType.SUCCESS, `Centro de Custo ${this.centro_custo.cc_cod} alterado com sucesso!`);
            this.$router.push('/cc');
          })
          .catch(err => {
            this.notify(NotificationType.DANGER, `${err.response.data.message}`);
          });
      } else {
        this.store
          .dispatch(POST_CC, this.centro_custo)
          .then(() => {
            this.notify(NotificationType.SUCCESS, `Centro de Custo ${this.centro_custo.cc_cod} criado com sucesso!`);
            this.$router.push('/cc');
          })
          .catch(err => {
            this.notify(NotificationType.DANGER, `${err.response?.data.message}`);
          })
      }
    },
  },
});
