
import { defineComponent, PropType } from "vue";
import ItemNav from './ItemNavInterface';

export default defineComponent({
  name: "MainNavItemComponent",
  props: {
    icon: {
      type: String,
    },
    label: {
      type: String,
      required: true,
    },
    isDropDown: {
      type: Boolean,
      required: true,
    },
    route: {
      type: String,
    },
    dropDownItems: {
      type: Array as PropType<ItemNav[]>,
      default: () => [{
        label: '',
        route: '',
      }],
    },
  },
  // data() {
  //   return {
  //     ddItems: this.dropDownItems,
  //   };
  // },
});
