
import { IUsuario } from "@/interfaces/IUsuario";
import { defineComponent, ref } from "vue";
import { useStore } from "@/store";
import { Form, Field } from "vee-validate";
import * as Yup from "yup";
import { USER_LOGIN } from "@/store/modules/usuario/constants/action-type";

export default defineComponent({
  name: "LoginComponentView",
  components: {
    Form,
    Field,
  },
  data() {
    const loginSchema = Yup.object().shape({
      iUsername: Yup.string().required("O campo E-MAIL  obrigatrio"),
      iPassword: Yup.string().required("O campo SENHA  obrigatrio"),
    });

    return {
      loginSchema,
    }
  },
  setup(){
    const user = ref({} as IUsuario);
    const store = useStore();

    return {
      user,
      store,
    }
  },
  methods: {
    handleLogin(){
      this.store
        .dispatch(USER_LOGIN, this.user)
        .then(() => {
            this.$router.push("/home");
          })
    }
  }
});
