
import { defineComponent, computed, ref } from "vue";
import { useStore } from "@/store";
import { GET_ALL_CC } from "@/store/modules/centrocusto/constants/action-type";
import { Form, Field } from "vee-validate";
import * as Yup from "yup";
import IFuncionario from "@/interfaces/IFuncionario";
import {
  POST_FUNC,
  PUT_FUNC,
} from "@/store/modules/funcionario/constants/action-type";
import useNotificator from "@/hooks/Notificator";
import { NotificationType } from "@/interfaces/INotification";
import ILinha from "@/interfaces/ILinha";
import IAparelho from "@/interfaces/IAparelho";
import ICentroCusto from "@/interfaces/ICentroCusto";

export default defineComponent({
  name: "FormularioFuncionariosViewComponent",
  components: {
    Form,
    Field,
  },
  props: {
    id: {
      type: String,
    },
  },
  setup(props) {
    const store = useStore();
    store.dispatch(GET_ALL_CC);

    const funcionario = ref({} as IFuncionario);
    const { notify } = useNotificator();

    const selectedCostCenter = ref('');
    // const numTel = ref('');

    if (props.id) {
      const employee = store.state.funcionario?.funcionarios.find(
        (empl) => empl.id == props.id
      );
      funcionario.value = employee as IFuncionario;
      selectedCostCenter.value = employee?.centro_custo_id || "";
    } else{
      funcionario.value.aparelhos = [] as IAparelho[];
      funcionario.value.linhas = [] as ILinha[];
    }

    return {
      store,
      notify,
      ccs: computed(() => store.state.centrocusto.ccs),
      funcionario,
      selectedCostCenter,
      // numeroTelefone: computed({
        // get() {
        //   return numTel.value;
        // },
        // set(linha) {
        //   numTel.value = `(${linha?.ddd}) ${linha?.numero.substring(0,5)}-${linha?.numero.substring(5, 9)}`
        //   return numTel;
        // }
      // }),
    };
  },
  data() {
    const funcionarioSchema = Yup.object().shape({
      iNome: Yup.string().required("O campo NOME  obrigatrio"),
      iSobrenome: Yup.string().required("O campo SOBRENOME  obrigatrio"),
      iNomeCracha: Yup.string().required(
        "O campo NOME NO CRACH  obrigatrio"
      ),
      iAdmissao: Yup.string().required("O campo ADMISSO  obrigatrio"),
      iNascimento: Yup.string().required("O campo NASCIMENTO  obrigatrio"),
      iRG: Yup.string().required("O campo RG  obrigatrio"),
      iCPF: Yup.string().required("O campo CPF  obrigatrio"),
      iCargo: Yup.string().required("O campo CARGO  obrigatrio"),
      iCentroCusto: Yup.string().required(
        "O campo CENTRO DE CUSTO  obrigatrio"
      ),
      iLinhas: Yup.array().of(
        Yup.string().required("O campo LINHA  obrigatrio")
      ),
      iAparelhos: Yup.array().of(
        Yup.string().required("O campo APARELHO  obrigatrio")
      ),
    });
    return {
      funcionarioSchema,
      // selectedCostCenter: '',
    };
  },
  methods: {
    saveFuncionario() {
      if (this.id) {
        //this.funcionario.centro_custo_id = this.selectedCostCenter;
        console.log(this.funcionario);
        this.store
          .dispatch(PUT_FUNC, this.funcionario)
          .then(() => {
            this.notify(
              NotificationType.SUCCESS,
              `Funcionario ${this.funcionario.nome_social} alterado com sucesso!`
            );
            this.$router.push("/funcionarios");
          })
          .catch((err) =>
            this.notify(NotificationType.DANGER, err.response.data)
          );
      } else {
        //this.funcionario.centro_custo_id = this.selectedCostCenter;
        console.log(this.funcionario);
        this.store
          .dispatch(POST_FUNC, this.funcionario)
          .then(() => {
            this.notify(
              NotificationType.SUCCESS,
              `Funcionario ${this.funcionario.nome_social} criado com sucesso!`
            );
            this.$router.push("/funcionarios");
          })
          .catch((err) =>
            this.notify(NotificationType.DANGER, err.response.data)
          );
      }
    },
    selectCostCenter(){
      console.log('Centro de Custo selecionado');
    },
    getNumeroTelefone(linha: ILinha){ 
      return `(${linha?.ddd}) ${linha?.numero.substring(0,5)}-${linha?.numero.substring(5, 9)}`
    }
  },
  computed: {
    employeeCostCenter() {
      return this.funcionario.centro_custo?.id || "";
    },
    numeroTelefone() {
      return (linha: ILinha) => {
        if(linha != undefined){
          return `(${linha?.ddd}) ${linha?.numero.substring(0,5)}-${linha?.numero.substring(5, 9)}`;
        } else {
          return 'N/A';
        }
      }
    },
  },
});
